/*Montserrat Alternates fonts*/
@font-face {
    font-family: 'Montserrat Alternates';
    font-style:  normal;
    font-weight: 400;
    font-display: swap;
    src: url("./fonts/Montserrat Alternates/MontserratAlternates-Regular.woff2") format("woff2"),
    url("./fonts/Montserrat Alternates/MontserratAlternates-Regular.woff") format("woff");
}

